html,body,#root{
  height: 100%;
  overflow: hidden;
}
html,body,section,.App{
  height: 100%;
  overflow: hidden;
}

.layout{
  margin: 0 auto;
  width: 600px;
}

@media (min-width: 700px) {
  .layout {
    width: 600px;
  }
}
@media (min-width: 900px) {
  .layout {
    width: 800px;
  }
}
@media (min-width: 1000px) {
  .layout {
    width: 900px;
  }
}
@media (min-width: 1200px) {
  .layout {
    width: 1100px;
  }
}

#page-music main{
  height: calc(100% - 24vh);
  padding-top: 12vh;
}

.background{
  position: absolute;
  z-index: -1;
  left: -10px;
  top: -10px;
  bottom: -10px;
  right: -10px;
  background: #444;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-filter: blur(4px);
          filter: blur(4px);
}

.background:before{
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0,0,0,0.4);
}

footer{
  height: 24vh;
  color: #fff;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 -.25vh .25vh .25vh rgba(255, 255, 255, 0.2);
}

footer>.layout{
  position: relative;
}

footer .music-box{
  height: 24vh;
  overflow: hidden;
  position: relative;
}

footer ul{
  position: absolute;
  left: 0;
}

footer .music-box li{
  float: left;
  margin-top: 1vh;
  width: 20vh;
  height: 20vh;
  margin: 2vh 2vh 0 2vh;
  text-align: center;
  cursor: pointer;
}

footer li.active{
  box-shadow: 0 0 .5vh .5vh rgba(255, 255, 255, 0.8);
}

footer .iconfont{
  position: absolute;
  top: 4vh;
  font-size: 8vh;
  opacity: 0;
  cursor: pointer;
  transition: all .4s;
}

footer .icon-back{
  left: -9vh;
}

footer .icon-right{
  right: -9vh;
}

footer:hover .iconfont{
  opacity: 1;
}

footer .iconfont:hover{
  color: rgba(255, 255, 255, 0.8);
  font-size: 9vh;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*::after{
  box-sizing: border-box;
}
*::before{
  box-sizing: border-box;
}
ul,ol,dl{
  list-style:none;
}
a{
  color: inherit;
}
footer .music-box div{
  height: 16vh;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

footer .music-box li:hover{
  box-shadow: 0 0 .5vh .5vh rgba(255, 255, 255, 0.8);
}

footer h3{
  font-size: 2vh;
  margin-top: 1vh;
}
main .music-panel{
  float: left;
}

main .music-panel>figure{
  width: 46vh;
  height: 46vh;
  background-image: url();
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

main .music-panel .actions{
  margin-top: 4vh;
  width: 100%;
}

main .music-panel .iconfont{
  float: left;
  width: 33%;
  text-align: center;
  font-size: 5vh;
  color:rgba(255,255,255,0.5);
  cursor: pointer;
  transition: all .4s
}

main .music-panel .iconfont:hover{
  color: #fff;
  font-size: 6vh;
}

main .music-panel .iconfont.active{
  color: #ff5722;
}

main .detail{
  margin-left: 56vh;
  color: #fff;
}

main .detail .tag{
  font-size: 2vh;
  padding: .4em .8em;
  background-color: rgb(3, 92, 119);
}

main .detail h1{
  font-size: 6vh;
  margin-top: 2vh;
}

main .detail .icons{
  display: inline-block;
  margin-top: 10vh;
  width: 80%;
}

main .icons li{
  float: left;
  width: 33%;
  text-align: left;
}

main .icons .iconfont{
  font-size: 3vh;
  margin-right: 1vh;
}

main .time-area{
  margin-top: 6vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

main .time-area .bar{
  position: relative;
  -webkit-flex: 1 1;
          flex: 1 1;
  height: .8vh;
  border-radius: .2vh;
  background-color: rgba(255,255,255,0.4);
  cursor: pointer;
}
main .time-area .bar-progress{
  position: absolute;
  height: .8vh;
  width: 50%;
  border-radius: .2vh;
  background: #fff;
  transition: width .8s;
}

main .time-area .current-time{
  width:3vh;
  margin-left: 1vh;
  font-size: 2vh;
}

main .author{
  font-size: 4vh;
  margin-top: 2vh;
  color: rgba(255, 255, 255, 0.6);
}

main .lyric{
  font-size: 2vh;
  margin-top: 3vh;
  color: rgba(255, 255, 255, 1);
}
